import dayjs from 'dayjs';
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Video',
  data() {
    return {
      time: ''
    };
  },
  created() {
    this.getTime();
  },
  methods: {
    getTime() {
      setInterval(() => {
        this.time = dayjs().format('YYYY-MM-DD HH:mm:ss');
      }, 1000);
    }
  }
};